nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  box-shadow: 0px 2px #ededed;
}
.logo {
  width: 200px;
}
.ham-menu {
  display: none;
}
.menu-list {
  display: flex;
  align-items: center;
}

.menu-items {
  margin-left: 20px;
}
.menu-link {
  font-size: 1.2rem;
  font-weight: 500;
  color: #6e6a83;
  transition: 0.3s ease;
}

.menu-link:hover {
  color: #0000ff;
}

/* For the tablet view */
@media screen and (max-width: 992px) {
  .logo {
    width: 160px;
  }
  .ham-menu {
    display: block;
    position: absolute;
    top: 20px;
    right: 15px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .menu-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 350px;
    height: 100vh;
    position: absolute;
    top: 110px;
    right: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .menu-list.active {
    background: #0000ff;
    backdrop-filter: blur(4.5px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    right: 0;
    opacity: 1;
    padding-top: 50px;
    transition: all 0.6s ease;
    z-index: 10;
  }
  .menu-items {
    padding: 20px 0 20px 40px;
    margin-left: 0;
  }
  .menu-link {
    color: #fff;
  }
  .menu-link:hover {
    color: #fff;
  }
}

/* For the mobile View */
@media screen and (max-width: 576px) {
  nav {
    padding: 20px 30px;
  }
  .logo {
    width: 100px;
  }
  .ham-menu {
    font-size: 1.2rem;
  }
  .menu-list {
    width: 220px;
    top: 80px;
  }
  .menu-list.active {
    padding-top: 0;
  }
  .menu-link {
    font-size: 1.1rem;
  }
}
